
import { defineComponent, ref } from 'vue';
import { QuickFetch, QuickData } from '@/quick';
import SmallModal from '@/components/SmallModal.vue';

export default defineComponent({
  setup(props, context) {
    const qf = new QuickFetch();
    const qd = new QuickData();
    const displayModal = ref(0);
    const userList: any = ref([]);
    const currentUrl = ref('');
    const currentUser: any = ref({});
    const isEdit = ref(false);
    function getUserList(url = 'account/user/list') {
      if (url !== null) {
        if (url.indexOf('account/user/list?page=') > -1) {
          // eslint-disable-next-line no-param-reassign
          url = url.substring(url.indexOf('account/user/list?page='));
        }
        qf.Url(url).GetAll(userList);
        currentUrl.value = url;
      }
    }
    getUserList();
    function getUser(id: number) {
      qf.Url(`account/user/list/${id}`).GetAll(currentUser).then(() => {
        displayModal.value = 1;
      });
    }
    function submitAll() {
      qf.Dataset()
        .Add('name', currentUser.value.name)
        .Add('permission', currentUser.value.permission)
        .Add('verification', currentUser.value.verification)
        .Add('phone', currentUser.value.phone);
      qf.Url('account/user/edit').Patch(currentUser.value.u_id, qf.GetDset()).then((res: any) => {
        if (res.message === 'done') {
          qd.Alert('已編輯');
          displayModal.value = 0;
          getUserList(currentUrl.value);
        }
      });
    }
    return {
      displayModal,
      userList,
      getUserList,
      getUser,
      currentUser,
      isEdit,
      submitAll,
    };
  },
  components: {
    SmallModal,
  },
});
