
import { defineComponent, ref } from 'vue';
import { QuickData, QuickFetch } from '@/quick';
import FullModal from '@/components/FullModal.vue';
import UserAccount from '@/components/account/UserAccount.vue';

export default defineComponent({
  setup() {
    const qf = new QuickFetch();
    const displayModal = ref(0);
    const adminData = JSON.parse(localStorage.sep5_admin_data);
    const factSheet: any = ref({});
    function getFactSheet() {
      qf.Url('reg-factsheet').GetAll(factSheet);
    }
    getFactSheet();
    return {
      displayModal,
      large: [1, 2, 3, 4, 5],
      factSheet,
      adminData,
    };
  },
  components: {
    FullModal,
    UserAccount,
  },
});
